.page-hotels{
    .section-top{
        background: url("../img/hero-hotels.jpg") center / cover;
    }
    .package-list{
        li:not(:last-child){
            border-bottom: 1px solid #eeeeee;
            padding-bottom: 6%;
        }
    }
    .section-hotels-destinations{
        @media screen and (max-width:992px){
            margin-bottom: 50%;
        }
    }
    .section-hotels-destinations-content{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 3%;
        @media screen and (max-width:992px){
            grid-template-columns: 100%;
            padding: 0 10%;
            gap: 1%;
        }
        .destination-info{
            padding: 6%;
        }
        .hotels-destination{
            background: $color_white;
            transition: all .3s ease-in-out;
            &:hover{
                box-shadow: 0 10px 30px 0px #868686;
                transition: all .3s ease-in-out;
            }
            p{
                text-align: left;
            }
        }
    }
    .destination-text{
        display: flex;
        justify-content: space-between;
    }
}